.grid {
    display: grid;
    grid-template-columns: auto 650px;
    grid-template-rows: 90px minmax(400px, auto) auto;
    grid-template-areas: 
      'header header'
      'main rightbar'
      'footer footer';
    column-gap: 20px;
    row-gap: 30px;
    background-color: rgb(239, 239, 239);
    margin: 0;
  }
  
  .page-header {
    grid-area: header;
  }
  
  .page-rightbar {
    grid-area: rightbar;
  }
  
  .page-main {
    grid-area: main;
  }
  
  .page-footer {
    grid-area: footer;
  }
  
  .content {
    color: #242424;
    background-color: rgb(239, 239, 239);
    font-weight: 600;
    text-align: center;
    box-sizing: border-box;
    height: 100%;
    padding: 10px;
  }
.p {
    text-align: left;
    margin-top: 20px;
    margin-left: 10px;
    font-size: 30px;
    font-family: Arial, Helvetica, sans-serif;
